
.main-box.detail {
  margin-bottom: 2.048rem;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.main-box.detail footer {
    height: 2.048rem;
    width: 100%;
    border-top: 1px solid #CCC;
    position: fixed;
    z-index: 99;
    bottom: 0;
    left: 0;
    text-align: center;
    font-weight: 700;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.main-box.detail footer .son-btn {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 16%;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      border-left: 1px solid #CCC;
}
.main-box.detail footer .son-btn.none {
        border: none;
}
.main-box.detail footer .son-btn span {
        display: block;
        width: 100%;
        font-size: 28px;
}
.main-box.detail footer .son-btn span.iconfont {
          font-size: 36px;
}
.main-box.detail footer div {
      width: 34%;
      color: #fff;
      padding-top: 5px;
      font: 600 28px/1.3em arial;
}
.main-box.detail footer div:first-of-type {
        background: rgba(255, 73, 100, 0.6);
}
.main-box.detail footer div:last-of-type {
        background: #ff4965;
}
.detail .swiper-slide {
  width: 100vw;
  height: 100vw;
}
.detail .swiper-pagination .swiper-pagination-bullet {
  width: .34133333rem;
  height: .34133333rem;
}
.detail .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
    background: #ff4965;
}
.info-box > * {
  background: #fff;
}
.info-box .area {
  padding: .3rem 0 0 .512rem;
}
.info-box .area .price span {
    display: inline-block;
    color: #fff;
    padding: 0 10px;
    margin-left: 10px;
    border-radius: 5px;
    font-size: 20px;
}
.info-box .area .price-new {
    font-weight: 600;
    color: #FF4965;
    font-size: 40px;
}
.info-box .area .price-old {
    margin-left: .256rem;
    display: inline-block;
    font-size: 26px;
    font-weight: 500;
    text-decoration: line-through;
    color: #8f8f8f;
}
.info-box .area .group {
    color: #8f8f8f;
    font-size: 24px;
    line-height: 1.5em;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-right: 20px;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.info-box .title {
  padding-right: .512rem;
  padding-left: .512rem;
  font-size: 30px;
  color: #3d3d3d;
  font-weight: 700;
}
.info-box .space-box {
  margin-top: 10px;
}
.info-box .promise {
  font-size: 24px;
  line-height: 2.5em;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.info-box .promise .iconfont {
    color: #FF4965;
    font-size: 20px;
}



